import React, { Component } from "react";
import Node from "./Node/Node";
import {
  dijkstra,
  getNodesInShortestPathOrderDijkstra,
} from "../algorithms/dijkstra";
import { DFS, getNodesInShortestPathOrderDFS } from "../algorithms/DFS";
import { BFS, getNodesInShortestPathOrderBFS } from "../algorithms/BFS";
import { Astar, getNodesInShortestPathOrderAtsar } from "../algorithms/Astar";
import {
  recursiveDivisionMaze,
  getwallnodes,
} from "../Mazes/recursiveDivisionMaze";
import "./PathfindingVisualizer.css";

var START_NODE_ROW = 5;
var START_NODE_COL = 10;
var FINISH_NODE_ROW = 15;
var FINISH_NODE_COL = 40;
var Animation_speed = 50;

export default class PathfindingVisualizer extends Component {
  constructor() {
    super();
    this.state = {
      grid: [],
      mouseIsPressed: false,
      selectedvalue: "Dijkstra",
      value: false,
    };
  }
  componentDidMount() {
    const grid = getInitialGrid();
    this.setState({ grid });
  }
  Refresh_all() {
    window.location.reload(false);
  }
  handleMouseDown(row, col) {
    if (this.state.value) return;
    var check = 0;
    if (row === START_NODE_ROW && col === START_NODE_COL) {
      check = 1;
    } else if (row === FINISH_NODE_ROW && col === FINISH_NODE_COL) {
      check = 2;
    }
    const newGrid = getNewGridWithWallToggled(this.state.grid, row, col, check);
    this.setState({ grid: newGrid, mouseIsPressed: true });
  }

  handleMouseEnter(row, col) {
    if (this.state.value) return;
    if (!this.state.mouseIsPressed) return;
    var check = 0;
    if (row === START_NODE_ROW && col === START_NODE_COL) {
      check = 1;
    } else if (row === FINISH_NODE_ROW && col === FINISH_NODE_COL) {
      check = 2;
    }
    const newGrid = getNewGridWithWallToggled(this.state.grid, row, col, check);
    this.setState({ grid: newGrid });
  }

  handleMouseUp() {
    if (this.state.value) return;
    this.setState({ mouseIsPressed: false });
  }
  invertbuttons() {
    const { value } = this.state.value;
    this.setState({ value: !value });
  }
  insertmaze() {
    var { grid } = this.state;
    const startNode = grid[START_NODE_ROW][START_NODE_COL];
    const finishNode = grid[FINISH_NODE_ROW][FINISH_NODE_COL];
    grid = recursiveDivisionMaze(grid, startNode, finishNode);
    const wallnodes = getwallnodes();
    for (let i = 0; i < wallnodes.length; i++) {
      const { row, col } = wallnodes[i];
      this.handleMouseDown(row, col);
      this.handleMouseEnter(row, col);
      this.handleMouseUp(row, col);
    }
  }
  animateDijkstra(visitedNodesInOrder, nodesInShortestPathOrder) {
    for (let i = 0; i <= visitedNodesInOrder.length; i++) {
      if (i === visitedNodesInOrder.length) {
        setTimeout(() => {
          this.animateShortestPathDijkstra(nodesInShortestPathOrder);
        }, 10 * i);
        return;
      }
      setTimeout(() => {
        const node = visitedNodesInOrder[i];
        document.getElementById(`node-${node.row}-${node.col}`).className =
          "node node-visited";
      }, (Animation_speed / 5) * i);
    }
  }
  animateShortestPathDijkstra(nodesInShortestPathOrder) {
    for (let i = 0; i < nodesInShortestPathOrder.length; i++) {
      setTimeout(() => {
        const node = nodesInShortestPathOrder[i];
        document.getElementById(`node-${node.row}-${node.col}`).className =
          "node node-shortest-path";
      }, Animation_speed * i);
    }
  }
  visualizeDijkstra() {
    this.invertbuttons();
    const { grid } = this.state;
    const startNode = grid[START_NODE_ROW][START_NODE_COL];
    const finishNode = grid[FINISH_NODE_ROW][FINISH_NODE_COL];
    const visitedNodesInOrder = dijkstra(grid, startNode, finishNode);
    const nodesInShortestPathOrder = getNodesInShortestPathOrderDijkstra(
      finishNode
    );
    this.animateDijkstra(visitedNodesInOrder, nodesInShortestPathOrder);
  }
  animateDFS(visitedNodesInOrder, nodesInShortestPathOrder) {
    for (let i = 0; i <= visitedNodesInOrder.length; i++) {
      if (i === visitedNodesInOrder.length) {
        setTimeout(() => {
          this.animateShortestPathDFS(nodesInShortestPathOrder);
        }, 20 * i);
        return;
      }
      setTimeout(() => {
        const node = visitedNodesInOrder[i];
        document.getElementById(`node-${node.row}-${node.col}`).className =
          "node node-visited";
      }, 20 * i);
    }
  }
  animateShortestPathDFS(nodesInShortestPathOrder) {
    for (let i = 0; i < nodesInShortestPathOrder.length; i++) {
      setTimeout(() => {
        const node = nodesInShortestPathOrder[i];
        document.getElementById(`node-${node.row}-${node.col}`).className =
          "node node-shortest-path";
      }, 50 * i);
    }
  }
  visualizeDFS() {
    this.invertbuttons();
    const { grid } = this.state;
    const startNode = grid[START_NODE_ROW][START_NODE_COL];
    const finishNode = grid[FINISH_NODE_ROW][FINISH_NODE_COL];
    const visitedNodesInOrder = DFS(grid, startNode, finishNode);
    const nodesInShortestPathOrder = getNodesInShortestPathOrderDFS(finishNode);
    this.animateDFS(visitedNodesInOrder, nodesInShortestPathOrder);
  }
  animateBFS(visitedNodesInOrder, nodesInShortestPathOrder) {
    for (let i = 0; i <= visitedNodesInOrder.length; i++) {
      if (i === visitedNodesInOrder.length) {
        setTimeout(() => {
          this.animateShortestPathBFS(nodesInShortestPathOrder);
        }, 10 * i);
        return;
      }
      setTimeout(() => {
        const node = visitedNodesInOrder[i];
        document.getElementById(`node-${node.row}-${node.col}`).className =
          "node node-visited";
      }, 10 * i);
    }
  }
  animateShortestPathBFS(nodesInShortestPathOrder) {
    for (let i = 0; i < nodesInShortestPathOrder.length; i++) {
      setTimeout(() => {
        const node = nodesInShortestPathOrder[i];
        document.getElementById(`node-${node.row}-${node.col}`).className =
          "node node-shortest-path";
      }, 50 * i);
    }
  }
  visualizeBFS() {
    this.invertbuttons();
    const { grid } = this.state;
    const startNode = grid[START_NODE_ROW][START_NODE_COL];
    const finishNode = grid[FINISH_NODE_ROW][FINISH_NODE_COL];
    const visitedNodesInOrder = BFS(grid, startNode, finishNode);
    const nodesInShortestPathOrder = getNodesInShortestPathOrderBFS(finishNode);
    this.animateBFS(visitedNodesInOrder, nodesInShortestPathOrder);
  }
  visualizeAstar() {
    this.invertbuttons();
  }

  render() {
    const { grid, mouseIsPressed } = this.state;
    return (
      <React.Fragment>
        <div class="navbar a">
          <h1>Pathfinding Visualizer</h1>
          <button class="butt" onClick={() => this.Refresh_all()}>
            <span>Refresh!</span>
          </button>
          <button
            class="button button1"
            disabled={this.state.value}
            onClick={() => this.visualizeDijkstra()}
          >
            Dijkstra
          </button>
          <button
            class="button button2"
            disabled={this.state.value}
            onClick={() => this.visualizeDFS()}
          >
            DFS
          </button>
          <button
            class="button button3"
            disabled={this.state.value}
            onClick={() => this.visualizeBFS()}
          >
            BFS
          </button>
          <button
            class="button button5"
            disabled={this.state.value}
            onClick={() => this.visualizeAstar()}
          >
            A* Search
          </button>
          <button
            class="button button4"
            disabled={this.state.value}
            onClick={() => this.insertmaze()}
          >
            Insert/Change Maze
          </button>
        </div>

        <div className="grid">
          {grid.map((row, rowIdx) => {
            return (
              <div key={rowIdx}>
                {row.map((node, nodeIdx) => {
                  const { row, col, isFinish, isStart, isWall } = node;
                  return (
                    <Node
                      key={nodeIdx}
                      col={col}
                      isFinish={isFinish}
                      isStart={isStart}
                      isWall={isWall}
                      mouseIsPressed={mouseIsPressed}
                      MouseDown={(row, col) => this.handleMouseDown(row, col)}
                      onMouseEnter={(row, col) =>
                        this.handleMouseEnter(row, col)
                      }
                      onMouseUp={() => this.handleMouseUp()}
                      row={row}
                    ></Node>
                  );
                })}
              </div>
            );
          })}
        </div>

        <div className="bottomright">
          <p>Made by: Ishank Mittal</p>
        </div>
      </React.Fragment>
    );
  }
}

const getInitialGrid = () => {
  const grid = [];
  for (let row = 0; row < 20; row++) {
    const currentRow = [];
    for (let col = 0; col < 50; col++) {
      currentRow.push(createNode(col, row));
    }
    grid.push(currentRow);
  }
  return grid;
};

const createNode = (col, row) => {
  return {
    col,
    row,
    isStart: row === START_NODE_ROW && col === START_NODE_COL,
    isFinish: row === FINISH_NODE_ROW && col === FINISH_NODE_COL,
    distance: Infinity,
    isVisited: false,
    isWall: false,
    previousNode: null,
  };
};

const getNewGridWithWallToggled = (grid, row, col, check) => {
  const newGrid = grid.slice();
  const node = newGrid[row][col];
  if (START_NODE_ROW === -1) {
    const newNode = {
      ...node,
      isStart: !node.isStart,
    };
    START_NODE_ROW = row;
    START_NODE_COL = col;
    newGrid[row][col] = newNode;
  } else if (FINISH_NODE_COL === -1) {
    const newNode = {
      ...node,
      isFinish: !node.isFinish,
    };
    FINISH_NODE_ROW = row;
    FINISH_NODE_COL = col;
    newGrid[row][col] = newNode;
  } else if (check === 1) {
    const newNode = {
      ...node,
      isStart: !node.isStart,
    };
    START_NODE_ROW = -1;
    START_NODE_COL = -1;
    newGrid[row][col] = newNode;
  } else if (check === 2) {
    const newNode = {
      ...node,
      isFinish: !node.isFinish,
    };
    FINISH_NODE_ROW = -1;
    FINISH_NODE_COL = -1;
    newGrid[row][col] = newNode;
  } else {
    const newNode = {
      ...node,
      isWall: !node.isWall,
    };
    newGrid[row][col] = newNode;
  }

  return newGrid;
};
