var generator = require("generate-maze");

var wallnodes = [];
function Check(startNode, finishNode, i, j) {
  if (startNode.row === i && startNode.col === j) return 0;
  if (finishNode.row === i && finishNode.col === j) return 0;
  return 1;
}
export function recursiveDivisionMaze(grid, startNode, finishNode) {
  var maze = generator(50, 20);
  for (let i = 0; i < 20; i++) {
    for (let j = 0; j < 50; j++) {
      if (maze[i][j].top && Check(startNode, finishNode, i, j)) {
        wallnodes.push(grid[i][j]);
      }
    }
  }
  return grid;
}
export function getwallnodes() {
  return wallnodes;
}
